import React from "react"
import { Link } from 'react-router-dom'
import "./workCard.css"

const WorkCard = (props) => (
        <div className="cardGroup">
            {/* Add a wrap for a parralax effect */}
            <div className="cardImageWrap">
            <a className="cardImage"    href={props.to}
                                        title={props.imageAlt}
                                        style={{backgroundImage: `url(${props.image})`,
                                        backgroundPosition: `center`,
                                        backgroundSize: `cover`,
                                        backgroundRepeat: `no-repeat`}}
            >
            </a>
            </div>
            <div className="infoGroup">
                <div className="companyName">{props.companyName + " • " + props.role}</div>
                <h1>{props.projectName}</h1>
                <p>{props.description}</p>
                <div className="infoLinkGroup">
                    {props.isExternal == "true" ? <a href={props.to} target="blank">{props.buttonName}</a> : <Link to={props.to}>{props.buttonName}</Link>}
                </div>
            </div>
        </div> 
)

export default WorkCard