import React from "react"
import { Helmet } from "react-helmet"
import Layout from "./components/layout/layout"
import Head from "./components/layout/head"


const YandexNews = () => (
    <Layout>
        <Helmet>
            <title>Yandex News Redisign: Story Page</title>
            <meta name="description" content="Case Study of Yandex News Story Page Redesign" data-react-helmet="true" />
            <meta name="keywords" content="redesign, yandex news, story page, ux design, ui design" data-react-helmet="true" />
            <link rel="canonical" href="http://frolovoleg.ru/yandex-news" />
        </Helmet>
        <Head 
            title="Yandex News (Story Page Redesign)"
            projectType="Web, iOS and Android apps"
            date="2014-2015"
            client="Yandex"
            role="Product Designer"
            cover={require("./images/yandex_news/cover@2x.jpg")}
        />
        <h2>Overview</h2>
        <p>
            Yandex News is the largest news service in Europe with an audience of more than 40 million people per day. It's a news aggregator for thousands of news sites.  
        </p>
        <div style={{backgroundImage: "url(" + require(`./images/yandex_news/01_yandex_news.png`) + ")"}} className="projectImg" ></div>
        <div className="note">
            Yandex is the Russian Google. Yandex products are top-rated in post-USSR countries and take a dominant position among user-end services.
        </div>
        <p>
            Each day, millions of people read the latest news about different topics with YandexNews. One of the most important pages in the service is the plot page. People come to this page from various sources. It could be the main page of Yandex Search links on partner sites, mobile widgets, and other Yandex apps. The plot page is the final point of a user's journey on the site. A user sees little news snippets that are compiled with machine learning algorithms, chooses one of them, and then goes to the site where the whole article lives. Yandex News is an essential part of the media ecosystem in Russia. More than 50% of Internet traffic from Russian news sites comes from Yandex.
        </p>
        <h2>Challenge</h2>
        <p>
            The previous Yandex News was an old project with a lot of legacies. Our main goal was to redesigned the whole site. I was responsible for applying new corporative UI guidelines and improving the UX.
        </p>
        <div style={{backgroundImage: "url(" + require(`./images/yandex_news/02_yandex_news.png`) + ")"}} className="projectImg"></div>
        <div className="note">
            Even though it looked like a typical website from the 90s, our users were used to the way it was. So, we had to be careful about redesigning the UI of the site and not breaking old UX patterns.   
        </div>
        <p>
            Because partners provided all the content for YandexNews, we needed to strike a balance between showing the user a comprehensive picture of the news and honoring the interests of our partners (to get the user on their site). We had to represent each partner equally on the story page.
        </p>
        <p>
            Most of the people who went to the story page clicked on the first link in a list of snippets. It was happening because they thought that it was the most relevant link in the list. Sometimes it was, but most of the time, it was a link to an article posted by a popular media outlet. Of course, we always improved our algorithms to recognize the most valuable source of news and to protect our users from any spam and to cheat our system.  
        </p>
        <p>
            Big media companies with audiences of millions that could post fast and frequent updates were almost always on the top of the list, even if the first news about the event appeared on a less popular news site. Also, we knew that our users had different favorite news sites they trusted, and sometimes they were small independent media sites. So we wanted to show as comprehensive a picture of the news as possible, including all points of view, of foreign and independent mass media.
        </p>
        <h2>UX Strategy</h2>
        <p>
            To meet the challenge, we did the following:
        </p>
        <ul>
            <li>
                Diminished the emphasis on the first news snippets, showing that they are just the newest ones, and there is a lot of other valuable information on the page. Therefore we could distribute a user's attention more evenly among other media;
            </li>
            <li>
                Allowed users to find their favorite news site in a list on the story page;
            </li>
            <li>
                Showed different points of view on the news: personal blogs, tweets, facebook posts, foreign media, etc;
            </li>
            <li>
                Personalised the user experience if we knew the user well (if the user was registered in our system).
            </li>
        </ul>
        <div style={{backgroundImage: "url(" + require(`./images/yandex_news/03_yandex_news.png`) + ")"}} className="projectImg" ></div>
        <div className="note">
            A list of consistent news snippets allowed users to scan the page fast.
        </div>
        <h2>UX Design</h2>
        <h3>Explore more</h3>
        <p>
            In the past, the first three snippets had a special place and a different design. It made a user think that they were the main snippets on the page and that the rest of the snippets weren’t important. This wasn’t true; they were just the newest. To fix that, we made the first three snippets look like the others. In the new design, the user saw a list of news snippets sorted by date.
        </p>
        <div style={{backgroundImage: "url(" + require(`./images/yandex_news/04_yandex_news.png`) + ")"}} className="projectImg" ></div>
        <div className="note">
            For an iPad version of the app, we decided to try a card approach. It allowed us to drop a vertical structure and distribute an importance weight among snippets more evenly.
        </div>

        <h3>Find your favorites</h3>
        <p>
            To make sure that a user could find their favorite news media, we made the icons bigger — from a barely noticeable 16 by 16 pixels to a quite prominent size of 48 by 48 pixels. Also, the icons were the same that our partners used for their mobile applications. It made it easy to recognize the company.
        </p>
        <div style={{backgroundImage: "url(" + require(`./images/yandex_news/05_yandex_news.png`) + ")"}} className="projectImg" ></div>
        <div className="note">
            One of the hardest challenges was increasing the size of these icons. We needed to rebuild the file-storing infrastructure of our service and redesign the admin site. We had to do a lot of work with our partners to get all the needed assets. 
        </div>

        <h3>Alternative points of view</h3>
        <p>
            We added a new section, allowing users to explore more news. Users could read foreign media or posts on social networks to get an alternative point of view.
        </p>
        <div style={{backgroundImage: "url(" + require(`./images/yandex_news/06_yandex_news.png`) + ")"}} className="projectImg" ></div>
        <div className="note">
            For example, we emphasized sections with news from other countries and posts from popular blogs. 
        </div>

        <h3>Personalized user experience</h3>
        <p>
            If authorized users added their favorite media and social networks to their profile, we marked their favorites and added links to related posts. It helped significantly to make user engagement better for this type of audience.
        </p>
        <div style={{backgroundImage: "url(" + require(`./images/yandex_news/07_yandex_news.png`) + ")"}} className="projectImg" ></div>
        <div className="note">
            It allowed helping a user to find the most related to their experience news. 
        </div>

        <h3>Enriched news snippets</h3>
        <p>
            We added new types of news snippets like broadcasting, similar news, a translation button for foreign media, alerts if the site was dangerous, and a super snippet for emergency news, etc.
        </p>
        <div style={{backgroundImage: "url(" + require(`./images/yandex_news/08_yandex_news.png`) + ")"}} className="projectImg" ></div>
        <div className="note">
            Broadcasting mode showed the whole picture of whatever happened with different points of view.
        </div>

        <h2>Results</h2>
        <p>
            The main goal was achieved — to enrich the UX by allowing our users to explore more without breaking user experience patterns based on the old version of the service. Analytics showed that people started to click on snippets that were not in the TOP3 more often. We brought diversity to their experience.
        </p>

    </Layout>
)

export default YandexNews