import React from "react"
import "./menu.css"

const scrollToWorks = () => {
    const executeScrollToWorks = () => window.scrollTo(0, document.getElementsByClassName("worksGroup")[0].offsetTop)
    return [executeScrollToWorks]
}

const scrollToPetProjects = () => {
    const executeScrollToPetProjects = () => window.scrollTo(0, document.getElementsByClassName("petProjectsGroup")[0].offsetTop)
    return [executeScrollToPetProjects]
}

const scrollToExperiments = () => {
    const executeScrollToExperiments = () => window.scrollTo(0, document.getElementsByClassName("experimentsGroup")[0].offsetTop)
    return [executeScrollToExperiments]
}

const scrollToTalksAndPublications = () =>
{
    const executeScrollToTalksAndPublications = () => window.scrollTo(0, document.getElementsByClassName("talksAndPublicationsGroup")[0].offsetTop)
    return [executeScrollToTalksAndPublications]
}

const Menu = () => {
    const [executeScrollToWorks] = scrollToWorks()
    const [executeScrollToPetProjects] = scrollToPetProjects()
    const [executeScrollToExperiments] = scrollToExperiments()
    const [executeScrollToTalksAndPublications] = scrollToTalksAndPublications()
    // useEffect(executeScroll, [])
    return (
    <div className="menuGroup">
        {/* <a href="/">Home</a> */}
        <a onClick={executeScrollToWorks}>Works</a>
        <a onClick={executeScrollToPetProjects}>Pet Projects</a>
        <a onClick={executeScrollToExperiments}>Experiments</a>
        <a onClick={executeScrollToTalksAndPublications}>Talks and publications</a>
        {/* <a href="/">Writings</a> */}
    </div>
    )
}

export default Menu